'use client';

import { User, Calendar as CalendarIcon, CreditCard } from 'lucide-react';

interface BookingWizardStepperProps {
    currentStep: number;
}

export default function BookingWizardStepper({ currentStep }: BookingWizardStepperProps) {
    const steps = [
        { id: 1, label: "Data Diri", icon: User },
        { id: 2, label: "Pilih Jadwal", icon: CalendarIcon },
        { id: 3, label: "Pembayaran", icon: CreditCard },
    ];

    return (
        <div className="w-full lg:w-1/4 space-y-10">
            <div>
                <h2 className="text-3xl font-bold text-[#D93F21]">Step {currentStep}</h2>
                <p className="text-gray-600 text-sm leading-relaxed mt-2">
                    {currentStep === 1 && "masukan data diri anda"}
                    {currentStep === 2 && "pilih jadwal pesanan anda"}
                    {currentStep === 3 && "pilih metode pembayaran anda"}
                </p>
            </div>

            <div className="relative space-y-8">
                {/* Garis Penghubung */}
                <div className="absolute left-[22px] top-2 bottom-2 w-0.5 bg-gray-200 -z-10"></div>

                {steps.map((item) => (
                    <div key={item.id} className="flex items-center gap-4 bg-white">
                        <div className={`
                            w-12 h-12 rounded-full flex items-center justify-center text-white shadow-lg transition-all duration-300
                            ${currentStep >= item.id ? 'bg-[#D93F21]' : 'bg-gray-300'}
                        `}>
                            <item.icon className="w-5 h-5" />
                        </div>
                        <span className={`font-medium text-lg ${currentStep === item.id ? 'text-gray-900' : 'text-gray-400'}`}>
                            {item.label}
                        </span>
                    </div>
                ))}
            </div>
        </div>
    );
}