'use client';

import { useState, useEffect } from 'react';
import axios from '@/lib/axios';
import { useRouter, useSearchParams } from 'next/navigation';
import { toast } from 'sonner';
import { useAuth } from '@/hooks/useAuth';
import { format } from 'date-fns';
import Link from 'next/link'; 
import { ArrowRight, ArrowLeft, Loader2 } from 'lucide-react';

// UI Components
import { Button } from '@/components/ui/button';

// Import Komponen Pecahan
import BookingWizardStepper from '@/components/pelanggan/booking/BookingWizardStepper';
import BookingWizardStep1 from '@/components/pelanggan/booking/BookingWizardStep1';
import BookingWizardStep2 from '@/components/pelanggan/booking/BookingWizardStep2';
import BookingWizardStep3 from '@/components/pelanggan/booking/BookingWizardStep3';

import { Booking, PaymentMethod } from '@/types';

export default function BookingWizardPage() {
    const router = useRouter();
    const searchParams = useSearchParams();
    const { user } = useAuth({ middleware: 'auth' });

    const [step, setStep] = useState(1);
    const [isSubmitting, setIsSubmitting] = useState(false);
    const [bookedDates, setBookedDates] = useState<Date[]>([]); 
    const [bookings, setBookings] = useState<Booking[]>([]);

    const [formData, setFormData] = useState({
        nama_lengkap: '',
        email: '',
        nomor_telepon: '',
        nama_club: '', 
        tanggal_booking: undefined as Date | undefined,
        jam_mulai: '',
        durasi_jam: '1',
        payment_method_id: '',
        asal_bank: '',      
        nama_pengirim: '',  
        jumlah_dp: '',
        bukti_pembayaran: null as File | null,
    });

    const [paymentMethods, setPaymentMethods] = useState<PaymentMethod[]>([]);
    const [lapanganId, setLapanganId] = useState<number | null>(null); 
    const [hargaPerJam, setHargaPerJam] = useState(0); 

    useEffect(() => {
        const initData = async () => {
            if (user) {
                setFormData(prev => ({
                    ...prev,
                    nama_lengkap: user.name,
                    email: user.email,
                    nomor_telepon: user.nomor_telepon || '',
                    nama_pengirim: user.name 
                }));
            }

            try {
                const resLap = await axios.get('/api/lapangans');
                if (resLap.data.length > 0) {
                    const lap = resLap.data[0];
                    setLapanganId(lap.id);
                    setHargaPerJam(Number(lap.harga_per_jam));
                }

                const resPay = await axios.get('/api/paymentMethods');
                setPaymentMethods(resPay.data.filter((p: any) => p.is_aktif));

                const resBook = await axios.get('/api/bookings');
                const dates = resBook.data.map((b: Booking) => new Date(b.tanggal_booking));
                setBookedDates(dates);
                setBookings(resBook.data);

            } catch (error: any) {
                console.error("Gagal memuat data:", error);
                toast.error("Gagal memuat data booking.");
            }
        };
        initData();

        const dateParam = searchParams.get('date');
        if (dateParam) {
            setFormData(prev => ({ ...prev, tanggal_booking: new Date(dateParam) }));
            setStep(2);
        }
    }, [user, searchParams]);


    const getJamSelesai = () => {
        if (!formData.jam_mulai) return '--:--';
        const [hours, minutes] = formData.jam_mulai.split(':').map(Number);
        const endHours = hours + Number(formData.durasi_jam);
        return `${String(endHours).padStart(2, '0')}:${String(minutes).padStart(2, '0')}`;
    };

    const totalHarga = Number(formData.durasi_jam) * hargaPerJam;

    const nextStep = () => {
        if (step === 1) {
            if (!formData.nama_club) return toast.error("Mohon isi Nama Club!");
        }
        if (step === 2) {
            if (!formData.tanggal_booking || !formData.jam_mulai) return toast.error("Pilih jadwal main!");
            if (!lapanganId) return toast.error("Data lapangan belum dimuat. Refresh halaman.");
        }
        setStep(prev => prev + 1);
        window.scrollTo(0, 0); 
    };

    const prevStep = () => setStep(prev => prev - 1);

    const handleSubmit = async () => {
        if (!formData.payment_method_id || !formData.bukti_pembayaran || !formData.asal_bank || !formData.nama_pengirim) {
            return toast.error("Lengkapi data pembayaran & upload bukti!");
        }

        if (!lapanganId) return toast.error("ID Lapangan tidak valid.");

        setIsSubmitting(true);
        try {
            const bookingPayload = {
                lapangan_id: lapanganId,
                tanggal_booking: format(formData.tanggal_booking!, 'yyyy-MM-dd'),
                jam_mulai: formData.jam_mulai,
                durasi_jam: parseInt(formData.durasi_jam), 
                payment_method_id: parseInt(formData.payment_method_id),
                acara: formData.nama_club,
                asal_bank: formData.asal_bank,
                nama_pengirim: formData.nama_pengirim
            };

            const resBooking = await axios.post('/api/bookings', bookingPayload);
            const bookingId = resBooking.data.id;

            const paymentData = new FormData();
            paymentData.append('jumlah_dp', formData.jumlah_dp || totalHarga.toString());
            paymentData.append('bukti_pembayaran', formData.bukti_pembayaran!);

            await axios.post(`/api/bookings/${bookingId}/payment`, paymentData);

            toast.success("Booking Berhasil!", { description: "Silakan tunggu konfirmasi admin." });
            router.push('/pelanggan/home');

        } catch (error: any) {
            const responseData = error.response?.data;
            const status = error.response?.status;

            if (status === 422) {
                if (responseData.errors?.jam_mulai) {
                    toast.error("Jadwal Bentrok!", { description: responseData.errors.jam_mulai[0] });
                    setStep(2); 
                } else if (responseData.errors?.lapangan_id) {
                     toast.error("Lapangan Tidak Ditemukan", { description: "ID Lapangan salah. Hubungi Admin." });
                } else {
                    toast.error("Validasi Gagal", { description: responseData.message || "Cek inputan Anda." });
                }
            } else {
                toast.error(responseData?.message || "Gagal memproses booking.");
            }
        } finally {
            setIsSubmitting(false);
        }
    };

    return (
        <div className="min-h-screen bg-white font-sans">

            <div className="max-w-6xl mx-auto px-4 md:px-8 py-12">
                <div className="flex flex-col lg:flex-row gap-16 mt-[100px]">
                    
                    {/* 1. Component Stepper */}
                    <BookingWizardStepper currentStep={step} />
                    <div className="flex-1">
                        <div className="border-l-2 border-[#D93F21] pl-8 md:pl-12 py-2 mb-10">
                            <h3 className="text-3xl font-bold text-[#D93F21] mb-2">
                                {step === 1 && "Data Diri"}
                                {step === 2 && "Pilih Jadwal"}
                                {step === 3 && "Pembayaran"}
                            </h3>
                            <p className="text-gray-500 text-sm">
                                {step === 1 && "masukan data diri anda dan lanjutkan ke langkah selanjutnya"}
                                {step === 2 && "pilih jadwal anda anda dan lanjutkan ke langkah selanjutnya"}
                                {step === 3 && "lakukan pembayaran dan masukan bukti pembayaran"}
                            </p>
                        </div>

                        {/* 2. Conditional Step Rendering */}
                        {step === 1 && (
                            <BookingWizardStep1 
                                formData={formData} 
                                setFormData={(newData: any) => setFormData(prev => ({ ...prev, ...newData }))} 
                            />
                        )}
                        {step === 2 && (
                            <BookingWizardStep2 
                                formData={formData} 
                                setFormData={(newData: any) => setFormData(prev => ({ ...prev, ...newData }))}
                                bookedDates={bookedDates}
                                getJamSelesai={getJamSelesai}
                                bookings={bookings}
                            />
                        )}
                        {step === 3 && (
                            <BookingWizardStep3 
                                formData={formData} 
                                setFormData={(newData: any) => setFormData(prev => ({ ...prev, ...newData }))}
                                paymentMethods={paymentMethods}
                                totalHarga={totalHarga}
                            />
                        )}

                        {/* 3. Action Buttons */}
                        <div className="flex gap-4 mt-12 justify-end pt-6 border-t border-gray-100">
                            {step > 1 ? (
                                <Button 
                                    variant="outline" 
                                    onClick={prevStep}
                                    className="bg-[#D93F21] text-white hover:bg-[#b9351b] hover:text-white border-none rounded-full px-8 h-12 text-base shadow-md"
                                >
                                    <ArrowLeft className="w-4 h-4 mr-2" /> Kembali
                                </Button>
                            ) : (
                                <Link href="/pelanggan/home">
                                    <Button 
                                        variant="outline" 
                                        className="bg-[#D93F21] text-white hover:bg-[#b9351b] hover:text-white border-none rounded-full px-8 h-12 text-base shadow-md"
                                    >
                                        Cancel
                                    </Button>
                                </Link>
                            )}

                            {/* Tombol Next / Submit */}
                            {step < 3 ? (
                                <Button 
                                    onClick={nextStep}
                                    className="bg-[#D93F21] hover:bg-[#b9351b] text-white rounded-full px-10 h-12 text-base font-bold shadow-lg hover:shadow-xl transition-all"
                                >
                                    Next <ArrowRight className="w-4 h-4 ml-2" />
                                </Button>
                            ) : (
                                <Button 
                                    onClick={handleSubmit}
                                    disabled={isSubmitting}
                                    className="bg-green-600 hover:bg-green-700 text-white rounded-full px-10 h-12 text-base font-bold shadow-lg w-48 hover:shadow-xl transition-all"
                                >
                                    {isSubmitting ? <Loader2 className="animate-spin mr-2" /> : "Booking Now"}
                                </Button>
                            )}
                        </div>

                    </div>

                </div>
            </div>
        </div>
    );
}