'use client';

import { format } from 'date-fns';
import { id as localeId } from 'date-fns/locale';
import { Calendar as CalendarIcon } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Calendar } from '@/components/ui/calendar'; 
import {
    Popover,
    PopoverContent,
    PopoverTrigger,
} from "@/components/ui/popover";
import { cn } from '@/lib/utils';
import { Booking } from '@/types';

interface BookingWizardStep2Props {
    formData: any;
    setFormData: (data: any) => void;
    bookedDates: Date[];
    getJamSelesai: () => string;
    bookings?: Booking[];
}

export default function BookingWizardStep2({ formData, setFormData, bookedDates, getJamSelesai, bookings = [] }: BookingWizardStep2Props) {
    
    const timeSlots = Array.from({ length: 16 }, (_, i) => {
        const hour = i + 8; 
        return `${hour.toString().padStart(2, '0')}:00`;
    });

    const isTimePassed = (time: string) => {
        if (!formData.tanggal_booking) return false;

        const now = new Date();
        const selectedDate = new Date(formData.tanggal_booking);
        
        const today = new Date();
        today.setHours(0, 0, 0, 0);
        selectedDate.setHours(0, 0, 0, 0);

        if (selectedDate.getTime() === today.getTime()) {
            const currentHour = now.getHours();
            const slotHour = parseInt(time.split(':')[0]);
            return slotHour <= currentHour;
        }

        return false;
    };

    const isSlotBooked = (time: string) => {
        if (!formData.tanggal_booking) return false;

        const dateStr = format(formData.tanggal_booking, 'yyyy-MM-dd');
        const slotHour = parseInt(time.split(':')[0]);

        return bookings.some(booking => {
            const bookingDateStr = format(new Date(booking.tanggal_booking), 'yyyy-MM-dd');
            if (bookingDateStr !== dateStr || booking.status_booking_id === 4) return false;

            const startHour = parseInt(booking.jam_mulai.split(':')[0]);
            let endHour: number;

            if (booking.jam_selesai) {
                endHour = parseInt(booking.jam_selesai.split(':')[0]);
            } else if (booking.durasi_jam) {
                endHour = startHour + Number(booking.durasi_jam);
            } else {
                endHour = startHour + 1; // fallback to 1 hour
            }

            return slotHour >= startHour && slotHour < endHour;
        });
    };

    return (
        <div className="space-y-6 max-w-lg animate-in slide-in-from-right-4 duration-300">
            
            <div className="space-y-2">
                <Label>Tanggal Main</Label>
                <Popover>
                    <PopoverTrigger asChild>
                        <Button
                            variant={"outline"}
                            className={cn(
                                "w-full justify-start text-left font-normal border-gray-300 bg-gray-50 h-12 rounded-lg",
                                !formData.tanggal_booking && "text-muted-foreground"
                            )}
                        >
                            <CalendarIcon className="mr-2 h-4 w-4" />
                            {formData.tanggal_booking ? format(formData.tanggal_booking, "PPP", { locale: localeId }) : <span>Pilih tanggal main</span>}
                        </Button>
                    </PopoverTrigger>
                    <PopoverContent className="w-auto p-0 bg-white rounded-xl shadow-2xl border-none" align="start">
                        <div className="p-4 bg-white rounded-xl">
                            <Calendar
                                mode="single"
                                selected={formData.tanggal_booking}
                                onSelect={(date) => setFormData({...formData, tanggal_booking: date})}
                                initialFocus
                                disabled={(date) => date < new Date(new Date().setHours(0, 0, 0, 0))}
                                modifiers={{
                                    booked: bookedDates
                                }}
                                modifiersStyles={{
                                    booked: { color: '#D93F21', fontWeight: 'bold', textDecoration: 'underline' }
                                }}
                                className="rounded-md border-none"
                                classNames={{
                                    head_cell: "text-gray-500 rounded-md w-10 font-normal text-[0.8rem] pb-4 pt-2",
                                    row: "flex w-full mt-2",
                                    cell: "h-10 w-10 text-center text-sm p-0 relative [&:has([aria-selected])]:bg-accent first:[&:has([aria-selected])]:rounded-l-md last:[&:has([aria-selected])]:rounded-r-md focus-within:relative focus-within:z-20",
                                    day: "h-10 w-10 p-0 font-normal aria-selected:opacity-100 hover:bg-gray-100 rounded-lg transition-all",
                                    day_selected: "bg-[#D93F21] text-white hover:bg-[#b9351b] hover:text-white focus:bg-[#D93F21] focus:text-white shadow-md",
                                    day_today: "bg-gray-100 text-gray-900 font-bold",
                                    day_outside: "text-gray-300 opacity-50",
                                    day_disabled: "text-gray-300 opacity-50",
                                }}
                            />
                        </div>
                    </PopoverContent>
                </Popover>
            </div>

            {/* SLOT WAKTU GRID */}
            <div className="space-y-3">
                <Label>Pilih Jam Mulai</Label>
                <div className="grid grid-cols-4 sm:grid-cols-5 gap-3">
                    {timeSlots.map((time) => {
                        const passed = isTimePassed(time);
                        const booked = isSlotBooked(time);
                        const disabled = passed || booked;
                        
                        return (
                            <button
                                key={time}
                                type="button"
                                disabled={disabled}
                                onClick={() => !disabled && setFormData({...formData, jam_mulai: time})}
                                className={`
                                    py-2 px-1 rounded-lg border text-xs sm:text-sm font-medium transition-all flex flex-col items-center justify-center gap-0.5
                                    ${booked
                                        ? 'bg-red-50 text-red-300 border-red-100 cursor-not-allowed opacity-60' // Update: Style Booked (Pudar & Merah Muda)
                                        : passed 
                                            ? 'bg-gray-50 text-gray-300 border-gray-100 cursor-not-allowed opacity-50' // Style Lewat (Abu Pudar)
                                            : formData.jam_mulai === time 
                                                ? 'bg-[#D93F21] border-[#D93F21] text-white shadow-md transform scale-105' // Style Selected
                                                : 'bg-white border-gray-200 text-gray-700 hover:border-[#D93F21] hover:text-[#D93F21]' // Style Available
                                    }
                                `}
                            >
                                <span>{time}</span>
                                {booked && <span className="text-[9px] font-bold uppercase">Booked</span>}
                            </button>
                        );
                    })}
                </div>
                {!formData.tanggal_booking && <p className="text-xs text-red-400 mt-1">* Pilih tanggal terlebih dahulu</p>}
            </div>

            <div className="grid grid-cols-2 gap-6">
                <div className="space-y-2">
                    <Label>Jam Mulai (Terpilih)</Label>
                    <div className="flex items-center px-4 border border-gray-200 bg-gray-100 h-12 rounded-lg text-gray-800 font-medium">
                        {formData.jam_mulai || '--:--'}
                    </div>
                </div>
                <div className="space-y-2">
                    <Label>Jam Akhir (Otomatis)</Label>
                    <div className="flex items-center px-4 border border-gray-200 bg-gray-100 h-12 rounded-lg text-gray-500">
                        {getJamSelesai()}
                    </div>
                </div>
            </div>

            <div className="space-y-2">
                <Label>Total Durasi (Jam)</Label>
                <Input 
                    type="number" 
                    min="1" 
                    max="5"
                    className="h-12 bg-gray-50 border-gray-300 rounded-lg"
                    value={formData.durasi_jam}
                    onChange={e => setFormData({...formData, durasi_jam: e.target.value})}
                />
            </div>
        </div>
    );
}